.grid {
    width: 100%;
    display: block;
    padding: 0;
  
    &.wide {
      max-width: 1520px;
      margin: 0 auto;
    }
  }
  
  .row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -4px;
    margin-right: -4px;
  
    &.no-gutters {
      margin-left: 0;
      margin-right: 0;
    }
  }
  
  .col {
    padding-left: 4px;
    padding-right: 4px;
  }
  
  .row.no-gutters .col {
    padding-left: 0;
    padding-right: 0;
  }
  
  .c-0 {
